export default{
  name: 'vue-footer',
  props: {
    active:{
      type:String,
      default:'home'
    }
  },
  data(){
    return {
      list:{
        home:{
          label:'首页',
          icon:'icon-home',
          _icon:'icon-homefill',
          path:'home',
          active:false
        },
        category:{
          label:'分类',
          icon:'icon-sort',
          _icon:'icon-sortfill',
          path:'category',
          active:false
        },
        cart:{
          label:'购物车',
          icon:'icon-cart',
          _icon:'icon-cartfill',
          path:'cart',
          active:false
        },
        my:{
          label:'我的',
          icon:'icon-my',
          _icon:'icon-myfill',
          path:'my',
          active:false
        }
      }
    }
  },
  created(){
    let key = this.active;
    this.list[key].active = true;
  }
}



