rgin-right: .3rem;\n}\n.cart-page .cart-list .item .box .info h5 {\n  color: #999;\n  font-size: .24rem;\n  margin-bottom: .12rem;\n}\n.cart-page .cart-list .item .box .info .del-btn {\n  font-size: .4rem;\n  color: #999;\n  margin-right: 0.25rem;\n}\n.cart-page .cart-list .tips {\n  font-size: .22rem;\n  color: #999;\n  border-bottom: 0.2rem solid #f6f6f6;\n  padding: .2rem .3rem;\n}\n.cart-page .cart-list .bargain {\n  background: #fff;\n}\n.cart-page .cart-list .bargain li {\n  padding-left: .3rem;\n  height: .8rem;\n  line-height: .8rem;\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: ellipsis;\n  border-bottom: 1px solid #f6f6f6;\n}\n.cart-page .cart-list .bargain li .check {\n  margin: 0 0.1rem;\n}\n.cart-page .cart-list .bargain li .check span {\n  font-size: 0.3rem;\n  color: #e58335;\n}\n.cart-page .cart-list .bargain li .btn {\n  border-left: 1px solid #f6f6f6;\n  padding: 0 0.2rem;\n  width: .8rem;\n  color: #eee;\n}\n.cart-page footer {\n  position: fixed;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  height: 1rem;\n  border-top: 1px solid #f6f6f6;\n  background: #FFF;\n  font-size: .26rem;\n  color: #999;\n}\n.cart-page footer div {\n  width: 2.4rem;\n  text-align: center;\n}\n.cart-page footer .btn {\n  height: 1rem;\n  line-height: 1rem;\n}\n.cart-page footer .buy {\n  background: #f2f2f2;\n  color: #333;\n  border-left: 1px solid #f2f2f2;\n  border-right: 1px solid #f2f2f2;\n}\n.cart-page footer .pay {\n  background: #e58335;\n  color: #fff;\n}\n.cart-page footer .price strong {\n  font-size: .4rem;\n  color: #e58335;\n  margin-right: .2em;\n}\n.cart-page footer .price strong em {\n  font-style: normal;\n  font-size: 0.26rem;\n  color: #999;\n}\n"],"sourceRoot":"webpack://"}]);

