export default{
  name: 'vue-count-plugin',
  props: {
    value:{
      type:[Number,Object],
      default:0
    },
    max:{
      type:Number,
      default:10000
    },
    min:{
      type:Number,
      default:1
    },
    step:{
      type:Number,
      default:1
    },
    disabled:{
      type:Boolean,
      default:false
    }
  },
  data(){
    return {
      val:0,
      maxStop:false,
      minStop:true
    }
  },
  watch:{
    val:function(newVal){
      this.$emit('input',newVal,this.value);
      if(this.val >= this.max){
        this.maxStop = true;
        return;
      }else{
        this.minStop = false;
      }
      if(this.val <= this.min){
        this.minStop = true;
        return;
      }else{
        this.maxStop = false;
      }
    }
  },
  methods:{
    add(){
      if(this.maxStop === true || this.disabled === true){
        return;
      }
      this.val += this.step;
    },
    sub(){
      if(this.minStop === true || this.disabled === true){
        return;
      }
      this.val -= this.step;
    }
  },
  created(){
    if(typeof this.value === 'object'){
      this.val = this.value.num;
    }else{
      this.val = this.value;
    }
  }
}



